const genderFilter = function (val) {
    return val == 1 ? '' : ''
}

const statusFilter = function (val) {
    switch (val) {
        case 0:
            val = ''
            break
        case 1:
            val = ''
            break
    }
    return val
}

const gradeFilter = function (val) {
    switch (val) {
        case 1:
            val = ''
            break
        case 2:
            val = ''
            break
        case 3:
            val = ''
            break
        case 4:
            val = ''
            break
        case 5:
            val = '';
            break
        case 6:
            val = '';
            break
    }
    return val
}

const termFilter = function (val) {
    switch (val) {
        case 1:
            val = ''
            break
        case 2:
            val = ''
            break
    }
    return val
}

export default {
    genderFilter,
    statusFilter,
    gradeFilter,
    termFilter
}


